import '../styles/main.css';

import colors from './data/colors.json'
import { CHANGE_BG_INTERVAL_DELAY } from './constants'
import { randomIntegerFromInterval } from './servises/rendom-integer-from-interval';

const refs = {
  startBtn: document.querySelector("[data-action='start']"),
  stopBtn: document.querySelector("[data-action='stop']"),
};

let randomBgStart = false;
let bgIntervalId = null;

const setRandomBodyBg = () => {
  const randomColorsIndex = randomIntegerFromInterval(0, colors.length - 1);
  document.body.style.backgroundColor = colors[randomColorsIndex];
}

const clearBodyBg = () => {
  document.body.style.backgroundColor = '';
}

const onStartBtnClick = () => {
  if (randomBgStart) return

  bgIntervalId = setInterval(setRandomBodyBg, CHANGE_BG_INTERVAL_DELAY);

  refs.startBtn.disabled = true;
  randomBgStart = true;
}

const onStopBtnClick = () => {
  clearInterval(bgIntervalId);

  clearBodyBg();

  refs.startBtn.disabled = false;
  randomBgStart = false;
}

refs.startBtn.addEventListener('click', onStartBtnClick);
refs.stopBtn.addEventListener('click', onStopBtnClick);